import React, { useState, useEffect } from "react";
import { useStore } from "react-redux";
import { read, diffDays, isAlreadyBooked } from "../actions/hotel";
import { getSessionId } from "../actions/stripe";
import moment from "moment";
import { useSelector, useDispatch } from "react-redux";
import { loadStripe } from "@stripe/stripe-js";
import { useHistory, Link } from "react-router-dom";
import { FcBinoculars,FcMultipleCameras,FcExternal, FcHome,FcKey,FcUndo, FcLock} from "react-icons/fc";
import { FaTwitter } from "react-icons/fa";
import {animateScroll as scroll} from 'react-scroll'

const ViewHotel = ({ match, history }) => {
  const [hotel, setHotel] = useState({});
  const [image, setImage] = useState("");
  const [image2, setImage2] = useState("");
  const [loading, setLoading] = useState(false);
  const dispatch = useDispatch();
 // const [alreadyBooked, setAlreadyBooked] = useState(false);

  const { auth } = useSelector((state) => ({ ...state }));

  useEffect(() => {
    loadSellerHotel();
  }, []);
{/*
  useEffect(() => {
    if (auth && auth.token) {
      isAlreadyBooked(auth.token, match.params.hotelId).then((res) => {
        // console.log(res);
        if (res.data.ok) setAlreadyBooked(true);
      });
    }
  }, []);
*/}
  const loadSellerHotel = async () => {
    let res = await read(match.params.hotelId);
    // console.log(res);
    setHotel(res.data);
    setImage(`${process.env.REACT_APP_API}/hotel/image/${res.data._id}`);
    setImage2(`${process.env.REACT_APP_API}/hotel/image2/${res.data._id}`);
  };

  const handleClick = async (e) => {
    e.preventDefault();

    if (!auth || !auth.token) {
      history.push("/login");
      return;
    }

    setLoading(true);
    if (!auth) history.push("/login");
    // console.log(auth.token, match.params.hotelId);
    let res = await getSessionId(auth.token, match.params.hotelId);
    // console.log("get sessionid resposne", res.data.sessionId);
    const stripe = await loadStripe(process.env.REACT_APP_STRIPE_KEY);
    stripe
      .redirectToCheckout({
        sessionId: res.data.sessionId,
      })
      .then((result) => console.log(result));
  };


  const logout = () => {
    dispatch({
      type: "LOGOUT",
      payload: null,
    });
    window.localStorage.removeItem("auth");
    history.push("/login");
  };


  return (
    <>


      <div className="container-fluid bg-secondary p-5 text-center" style={{backgroundImage:`url(${image2})`,  backgroundPosition: 'center', backgroundSize: 'cover'}}>
      <p style={{textAlign: 'left',fontSize:"3rem"}}><a href="/"><FcUndo /></a></p>
        <h1 style={{color:'#f7a440',fontWeight:'bold'}}>{hotel.title}</h1>
      </div>
      <div className="container-fluid">
        <div className="row justify-content-center">
            <img 
            src={image} 
            alt={hotel.title} 
            onClick={handleClick}
            className="img img-fluid m-2"
            style={{height: '20em',width: '20em'}}
             />
         


            <br />
            <p className="alert mt-3" style={{fontWeight: 'bold', color:"#ffdf6b",fontSize:"3rem",textAlign:"center"}}>${hotel.price}</p>

          </div>
        </div>

       

<hr />
<div class="container d-flex">
  <div class="col">


  <li className='list-inline-item'>
   <a href="/" className>
   <FcHome size='3rem' className="moves" style={{fontColor:"#81b214"}}/>
   </a>
   </li>
   
   {auth !== null && (
    <li className='list-inline-item'>
     <a className="nav-link" href="/dashboard">
     <FcBinoculars size="3em" className="moves" />
     </a>
     </li>
   )}

   {auth !== null && (
    <li className='list-inline-item'>
     <a className="pointer" href="#" onClick={logout}>
       <FcLock size="3rem" className="moves" />
     </a>
     </li>
   )}

   {auth === null && (
     <>
     <li className='list-inline-item'>
       <a href="/login">
       <FcKey size='2rem' className="moves" />
       </a>
       </li>

     </>
   )}


        <li className='list-inline-item'>
        <a href="https://www.instagram.com/grindergifts/" className>
          <FcMultipleCameras
            fab
            size='4rem'
            className='white-text p-1 m-2 moves'
          />
        </a></li>
        <a href="https://www.twitter.com/grindergifts/" className>
        <li className='list-inline-item'>
          <FaTwitter
            size="3rem"
            className='moves p-1 m-2'
          />
       </li></a>
        <li className='list-inline-item'>
          <FcExternal
            fab
            icon='google-plus'
            size='4rem'
            className='white-text p-1 m-2 moves'
            onClick={() => scroll.scrollToTop()}
            style={{cursor:"pointer"}}
          />
        </li>
  
    </div>
  </div>


  
    </>
  );
};

export default ViewHotel;
